:root {
  --accent: #009026;
  --accent-2: #EDFFEB;
  --heading: #27272E;
  --text-1: #425466;
  --text-2: #7A7A9D;
  --text-light: #ffffff;
  --background-primary: #F7FAFC;
  --background-secondary: #ffffff;
  --borders: #EDF2F7;
  --active: #68DBF2;
  --complete: #2ECF88;
  --awaiting: #F7936F;
  --drafts: #FAB592;
  --invoice: #FE6F7E;
  --quotes: #4CAFD0;
}

body.menu-closed .top-bar {
  max-width: calc(100vw - 90px);
}
body .top-bar {
  width: 100vw;
  max-width: calc(100vw - 250px);
  margin: 0 0 0 auto;
  transition: max-width 0.3s ease-in-out;
}
body .top-bar .inside-top-bar {
  margin: 0;
  height: 80px;
  display: flex;
  align-items: center;
  max-width: 100%;
  padding: 0 46px 0 20px;
  flex-wrap: nowrap;
}
body .top-bar .inside-top-bar .widget:nth-child(2n+1) {
  order: unset;
}
body .top-bar .inside-top-bar .AjaxSearchLiteWidget {
  margin: 0 auto 0 12%;
}
body .top-bar .inside-top-bar .page-title p {
  display: none;
}
body .top-bar .inside-top-bar .page-title h1 {
  font-size: 1.75rem;
  line-height: 39px;
  margin-bottom: 0px;
}
body .top-bar .inside-top-bar .user-account-avatar-outer > .wp-block-group__inner-container > p {
  display: none;
}
body .top-bar .inside-top-bar .user-account-avatar-outer #user-account-avatar p {
  border-radius: 50%;
  margin-bottom: 0;
  line-height: 0;
  overflow: hidden;
}

#masthead {
  height: 100vh;
  max-width: 250px;
  flex-wrap: wrap;
  position: fixed;
  left: 0;
  top: 0;
  padding-top: 20px;
  overflow: hidden;
  border: 1px solid var(--borders);
  transition: max-width 0.3s ease-in-out;
}
#masthead #burger-menu svg {
  cursor: pointer;
  margin-bottom: 14px;
}
#masthead .inside-header {
  flex-wrap: wrap;
  padding: 0 25px;
  height: 100%;
  align-items: flex-start;
}
#masthead .inside-header .site-branding-container {
  flex-wrap: wrap;
}
#masthead .inside-header .site-branding-container .site-branding a {
  opacity: 1;
  transition: all 0.2s ease-in-out;
}
#masthead .inside-header .site-branding-container #burger-menu {
  width: 100%;
}
#masthead .inside-header .header-scroll-content {
  width: 100%;
  height: calc(100% - 170px);
  position: relative;
}
#masthead .inside-header .header-scroll-content .menu-main-menu-container {
  margin-left: 0;
  margin-bottom: 30px;
  width: 100%;
  border-bottom: 1px solid var(--borders);
  padding-bottom: 30px;
}
#masthead .inside-header .header-scroll-content .menu-main-menu-container ul {
  list-style: none;
  margin: 0;
  padding-left: 0;
}
#masthead .inside-header .header-scroll-content .menu-main-menu-container ul li {
  position: relative;
}
#masthead .inside-header .header-scroll-content .menu-main-menu-container ul li .current-menu-item a {
  color: var(--accent);
}
#masthead .inside-header .header-scroll-content .menu-main-menu-container ul li a {
  padding: 0;
  display: flex;
  align-items: center;
  line-height: 34px;
  text-decoration: none;
  color: var(--text-1);
}
#masthead .inside-header .header-scroll-content .menu-main-menu-container ul li a span {
  opacity: 1;
  transition: opacity 0.2s ease-in-out, font-size 0.2s ease-in-out;
}
#masthead .inside-header .header-scroll-content .menu-main-menu-container ul li a svg {
  margin-right: 20px;
  transition: margin-right 0.3s ease-in-out;
  min-width: 14px;
  height: 14px;
}
#masthead .inside-header .header-scroll-content .menu-main-menu-container ul li a:hover {
  color: var(--accent);
}
#masthead .inside-header .header-scroll-content .menu-main-menu-container ul li a:hover svg path {
  fill: var(--accent);
}

body.menu-closed #masthead {
  max-width: 90px;
}
body.menu-closed #masthead .inside-header .site-branding-container .site-branding a {
  opacity: 0;
  font-size: 0;
}
body.menu-closed #masthead .header-scroll-content .menu-main-menu-container ul li a span {
  opacity: 0 !important;
  font-size: 0rem;
  transition: opacity 0.2s ease-in-out, font-size 0.3s ease-in-out 0.2s;
}
body.menu-closed #masthead .header-scroll-content .menu-main-menu-container ul li a svg {
  margin-right: 0px !important;
}

.log-out-link {
  display: flex;
  position: absolute;
  bottom: 18px;
  text-decoration: none;
}
.log-out-link p {
  font-size: 0.875rem;
  margin-left: 14px;
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

.menu-closed .log-out-link p {
  opacity: 0;
}

.upgrade-account {
  position: absolute;
  bottom: 60px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-end;
  order: 5;
  min-height: 85px;
  background: radial-gradient(circle at top, #009026 0, #0e4f1f 200%);
  border-radius: 24px;
  position: relative;
  width: 100vw;
  max-width: 228px;
  margin: 0 -15px;
}
.upgrade-account p {
  margin-bottom: 0;
  color: var(--text-light);
  font-size: 1rem;
  margin-bottom: 17px;
}
.upgrade-account img {
  width: 100%;
  height: 100%;
  max-width: 70px;
  max-height: 70px;
  border-radius: 50%;
  border: 4px solid var(--text-light);
  position: absolute;
  top: -30px;
}

.checklist-outer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 24px;
  width: 100%;
}
.checklist-outer .checklist-icon {
  opacity: 0;
  background-color: transparent;
  width: 0px;
  height: 0px;
  border-radius: 50%;
  display: grid;
  place-content: center;
  transition: opacity 0.2s ease-in-out 0.1s, background-color 0.2s ease-in-out 0.2s;
}
.checklist-outer .percentage {
  font-size: 0.688rem;
  width: 100%;
  overflow: hidden;
  color: var(--accent);
  opacity: 1;
  font-weight: 700;
  margin: 0 auto 8px 0;
  transition: width 0.3s ease-in-out, opacity 0.3s ease-in-out;
}
.checklist-outer .title-and-count {
  display: flex;
  position: relative;
  width: 100%;
  color: var(--heading);
  font-weight: 500;
  margin-bottom: 1px;
}
.checklist-outer .title-and-count p {
  margin: 0;
  font-size: 0.875rem;
  opacity: 1;
  white-space: nowrap;
  transition: opacity 0.3s ease-in-out, width 0.3s ease-in-out;
}
.checklist-outer .title-and-count .fraction {
  background-color: var(--accent-2);
  border-radius: 2em;
  height: 20px;
  max-width: 39px;
  text-align: center;
  font-size: 0.75rem;
  padding: 0px 10px;
  display: flex;
  align-items: center;
  color: var(--accent);
  font-weight: 700;
  position: absolute;
  right: 0;
  top: 0;
}
.checklist-outer p {
  width: 100%;
}
.checklist-outer .checklist-progress-bar {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  width: 100%;
  height: 12px;
  border-radius: 2em;
  overflow: hidden;
  background-color: var(--borders);
  margin-right: auto;
  transition: width 0.3s ease-in-out, opacity 0.3s ease-in-out;
}
.checklist-outer .checklist-progress-bar span.active {
  background-color: var(--accent);
}
.checklist-outer .checklist-progress-bar span.active.final {
  border-top-right-radius: 2em;
  border-bottom-right-radius: 2em;
}

#account-checklist {
  display: flex;
  flex-direction: column;
  order: 4;
  overflow: hidden;
  max-height: 800px;
  opacity: 1;
  white-space: nowrap;
  transition: opacity 0.3s ease-in-out, max-height 0.3s ease-in-out 0s;
  margin-bottom: 50px;
}
#account-checklist .checklist-item {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  position: relative;
}
#account-checklist .checklist-item label {
  pointer-events: none;
  font-size: 0.75rem;
  display: flex;
  align-items: center;
}
#account-checklist .checklist-item label:before {
  content: "";
  width: 14px;
  height: 14px;
  border-radius: 50%;
  border: 1px solid var(--heading);
  position: absolute;
  left: 0;
}
#account-checklist .checklist-item input {
  pointer-events: none;
  margin-right: 10px;
  opacity: 0;
}
#account-checklist .checklist-item input:checked ~ label {
  color: var(--accent);
}
#account-checklist .checklist-item input:checked ~ label:before {
  background-color: var(--accent);
  background-image: url("data:image/svg+xml,%3Csvg width='7' height='6' viewBox='0 0 7 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M5.57021 0.632554C5.79903 0.403732 6.17003 0.403732 6.39885 0.632554C6.62487 0.858571 6.62764 1.22329 6.40717 1.45271L3.28826 5.35135C3.28375 5.35698 3.27894 5.36235 3.27385 5.36744C3.04503 5.59627 2.67403 5.59627 2.44521 5.36744L0.377672 3.29991C0.148849 3.07109 0.148849 2.70009 0.377672 2.47127C0.606495 2.24244 0.97749 2.24244 1.20631 2.47127L2.84187 4.10683L5.55467 0.650082C5.55949 0.643934 5.56468 0.638081 5.57021 0.632554Z' fill='white'/%3E%3C/svg%3E");
  background-position: center;
  background-size: 8px;
  background-repeat: no-repeat;
  border: none;
}

.menu-closed #account-checklist {
  opacity: 0;
  max-height: 0;
  transition: opacity 0.3s ease-in-out, max-height 0s ease-in-out 0.3s;
}
.menu-closed .checklist-outer .checklist-icon {
  opacity: 1;
  width: 18px;
  height: 18px;
  background-color: var(--accent);
  margin-bottom: 6px;
}
.menu-closed .checklist-outer .title-and-count > p {
  opacity: 0;
  width: 0%;
}
.menu-closed .checklist-outer .percentage {
  width: 0%;
  opacity: 0;
}
.menu-closed .checklist-outer .checklist-progress-bar {
  opacity: 0;
}

html body {
  display: flex;
  flex-wrap: wrap;
}
html body.admin-bar #masthead {
  top: 32px;
}
html body.menu-closed #page, html body.menu-closed .site-footer {
  max-width: calc(100vw - 90px);
}
html body.menu-closed .site-footer {
  order: 4;
  width: 100vw;
}
html body #page {
  order: 3;
  max-width: calc(100vw - 250px);
  margin: 0 0 0 auto;
  width: 100vw;
  transition: max-width 0.3s ease-in-out;
}
html body .site-footer {
  order: 4;
  max-width: calc(100vw - 250px);
  width: 100vw;
  margin-left: auto;
  transition: max-width 0.3s ease-in-out;
}